<template>
  <div class="moditypwd">
    <j-header title="修改密码"></j-header>
    <main>
      <ul>
        <li>
          <p>旧登录密码</p>
          <jun-input v-model="form.oldpassword"
            placeholder="请输入旧密码"
            type='password'
            clear>
          </jun-input>
        </li>
        <li>
          <p>新登录密码</p>
          <jun-input v-model="form.password"
            placeholder="请输入新密码"
            type='password'
            clear>
          </jun-input>
        </li>
        <li>
          <p>确认密码</p>
          <jun-input v-model="form.confirmpwd"
            placeholder="请确认新密码"
            type='password'
            clear>
          </jun-input>
        </li>
      </ul>
      <button @click="modifi">确认修改</button>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JunInput from '@/components/JunInput'
import { verifyPassword } from '@/utils/validate'
import { getUid } from '@/utils/auth'
import { mapActions } from 'vuex'

export default {
  name: 'moditypwd',
  components: {
    JHeader,
    JunInput
  },
  data () {
    return {
      form: {
        oldpassword: '',
        password: '',
        confirmpwd: ''
      }
    }
  },
  created () {
  },
  methods: {
    ...mapActions([
      'LogOut'
    ]),
    init (data) {
      this.request({
        url: `/user/${getUid()}`,
        method: 'put',
        data
      }).then(res => {
        res.code === 1 && this.LogOut() && this.$toast('修改成功，请重新登录！')
      })
    },
    modifi () {
      const { oldpassword, password, confirmpwd } = this.form
      if (!oldpassword) {
        this.$toast({
          message: '请输入旧密码！',
          position: 'top'
        })
      } else if (!password) {
        this.$toast({
          message: '请输入新密码！',
          position: 'top'
        })
      } else if (!confirmpwd) {
        this.$toast({
          message: '请输入确认密码！',
          position: 'top'
        })
      } else if (!verifyPassword(oldpassword) && !verifyPassword(password) && !verifyPassword(confirmpwd)) {
        this.$toast({
          message: '密码格式不正确!',
          position: 'top'
        })
      } else if (password !== confirmpwd) {
        this.$toast({
          message: '新密码与旧密码不一致！',
          position: 'top'
        })
      } else {
        this.$confirm({
          title: '提示',
          message: '您确定要修改密码么？'
        }).then(() => {
          this.init({ oldpassword, password })
        }).catch(() => {
          this.$toast({
            message: '已取消操作！',
            position: 'top'
          })
        })
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
