<template>
    <header :style="{'background' : bg}">
        <span class="left-arrow wave" @click="goback" v-show="show"></span>
        <p v-if="title">{{ title }}</p>
        <slot></slot>
        <div class="svg-box" @click="handleIcon">
          <svg-icon :icon-class="icon" v-show="show" class="wave"/>
        </div>
    </header>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'home',
  components: {
  },
  props: {
    title: {
      type: String,
      default: '标 题'
    },
    icon: {
      type: String,
      default: 'home'
    },
    bg: {
      type: String,
      default: 'linear-gradient(90deg, #3b95f4 0%, #3f68db 100%)'
    },
    iconRouter: {
      type: [String, Object],
      default: 'homepage'
    },
    show: {
      type: Boolean,
      default: true
    },
    go: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  },
  created () {
  },
  methods: {
    goback () {
      if (this.go) {
        this.$router.go(-1)
      } else {
        this.$emit('goback')
      }
    },
    handleIcon () {
      if (typeof this.iconRouter === 'object') {
        this.$router.push({ name: this.iconRouter.router, params: this.iconRouter.params })
      } else {
        this.$router.push({ name: this.iconRouter })
      }
    }
  }
}
</script>

<style scoped lang="scss">
    @import '@/style/mixin.scss';
    header {
        @include fb(center-row);
        @include wh(100%, 0.92rem);
        position: relative;
        .left-arrow{
            padding-left: 0.5rem;
            position: absolute;
            top: 50%;
            left: 0.1rem;
            transform: translate(0, -50%);
            color:#ffffff;
        }
        .left-arrow:before {
            content: "";
            position: absolute;
            width: 0.23rem;
            height: 0.23rem;
            border: 1px solid #ffffff;
            border-width: .03rem 0 0 .03rem;
            -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
            top: -0.1rem;
            left: 0.16rem;
            // font-size: 0.16rem;
        }
        p {
            color: #fffefe;
            font-size: 0.4rem;
        }
        .svg-box{
            position: absolute;
            top: 50%;
            right: 0.1rem;
            transform: translate(0, -50%);
            color:#ffffff;
        }
    }
</style>
